.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  /* display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin); */
  color: white;
}



.App-link {
  color: #61dafb;
}

.wrapper {
  display: flex;
  justify-content: center;
  padding-top: 10rem;
}

.video-container {
  position: relative;
  height: 60vh;
}

.video-container video {
  position: relative;
  height: 60vh;
  z-index: 10;
}

.video-container canvas {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
